    'hedderitem': Hedder,
  },
  watch: {
    '$route' (to, from) {
      let id = Number;
      id = this.setId(to);
      this.setData(id);
    }
  },
  created:function() {
    let path = this.$router.currentRoute.path;
    let id = Number;
    id = this.setId(path);
    this.setData(id);
  },

  methods: {
    setId(path) {
      let id = Number;
      // 
      if ( path == this.routes[0]) {
        id  = 0;
      } else {
        // Id
        id  = this.$route.params['id'];
      };

      return id;
    },
    setData(id) {
      let target = Array
      // 
      if (id == 0 || id == 1) {
        target = this.kankens[0].kanken3;
      } else if (id == 2) {
        target = this.kankens[1].kankenJ2;
      } else if (id == 3) {
        target = this.kankens[2].kanken2;
      } else if (id == 4) {
        target = this.kankens[3].kankenJ1;
      } else if (id == 5) {
        target = this.kankens[4].kanken1;
      };


      // shuffle
      target = this.shuffleArry(target);
      // Object.assign
      Object.assign(this.kanjiList, target);
    },
    // 
    shuffleArry(kanken) {
      for(var i = kanken.length - 1; i > 0; i--){
        var r = Math.floor(Math.random() * (i + 1));
        var tmp = kanken[i];
        kanken[i] = kanken[r];
        kanken[r] = tmp;
      }
      return kanken
    }
  },
};
</script>

<style scoped>
  #app {
    height: 100vh;
    width: 100%;
    overflow: hidden;
  }

  #main {
    position: relative;
    height: calc(100% - 60px);
    min-height: 400px;
  }

  #footer {
    position: relative;
    height:60px;
    width: 100%;
    background: rgb(1, 18, 63);
  }

  #footer::before {
    content:"";
    position: absolute;
    bottom: 0;
    height: 80px;
    width: 100%;
    border-top: 10px solid rgb(1, 18, 63);
  }

  /*  */
  .fade-enter-to {
    opacity: 1;
  }
  /*  */
  .fade-enter-active {
    transition: all .5s 0s ease-in;
  }
  .fade-leave {
    opacity: 1;
  }
  .fade-leave-to {
    opacity: 0;
  }
  .fade-leave-active {
    transition: all .5s 0s ease-in;
  }
</style>



